.dropContainer {
  display: flex;
  justify-content: space-around;
}

.productBacklog {
  min-height: 512px;
  width: 100%;
}

.message {
  font-size: 20px;
}

.messageWrong {
  color: red;
}

.messageCorrect {
  color: green;
}

.modal__test--ok {
  display: none;
}

.modal__test--ok.open {
  display: flex;
}

.modal__test--wrong {
  display: none;
}

.modal__test--wrong.open {
  display: flex;
}
